import React from 'react';
import {Input,Row,Col} from "reactstrap";
import "bootstrap/dist/css/bootstrap.css";
import { Component, useState, useEffect } from "react";
import { IconButton, Select } from "@mui/material";




export default function UserProfile (){

    const [emailId, setEmailId] = useState("");
    const [uid, setUID] = useState("");
    const [fullName, setFullName] = useState("");
    const [city, setCity] = useState("");
    const [province, setProvince] = useState("");
    const [country, setCountry] = useState("");
    const [aboutMe, setAboutMe] = useState("");
    const [displayName, setDisplayName] = useState("");
    const [query, setQuery] = React.useState("");
    const [imageURL, setImageURL] = useState();
    const [serviceType, setServiceType] = useState("mortgage");
    
    return (
<div className="content">
    <Row>
          <Col md="6">
                  <Row>
                     <Col className="px-md-1" md="4">
                
                        <label>UID</label>
                        <Input
                          value={uid}
                          InputProps={{
                            readOnly: true,
                          }}
                          type="text"
                        />
                      
                    </Col>
                    <Col className="pr-md-1" md="4">
                        <label>Name</label>
                        <Input
                          value={fullName}
                          type="text"
                        />
                    </Col>
                    <Col className="pl-md-1" md="4">
                        <label htmlFor="exampleInputEmail1">
                          Email address
                        </label>
                        <Input
                          defaultValue=""
                          placeholder=""
                          value={emailId}
                          InputProps={{
                            readOnly: true,
                          }}
                          type="email"
                        />
                    </Col>
                </Row>
                  <Row>
                    <Col className="pr-md-1" md="4">
                      
                        <label>City</label>
                        <Input
                          value={city}
                          type="text"
                        />
                      
                    </Col>
                    <Col className="pr-md-1" md="4">
                      
                      <label>Province</label>
                      <Input
                        value={province}
                        type="text"
                      />
                    
                  </Col>
                    <Col className="px-md-1" md="4">
                        <label>Country</label>
                        <Input
                          defaultValue=""
                          placeholder=""
                          value={country}
                          type="text"
                        />
                    </Col>
                  </Row>
                  <Row>
                    <Col md="8">
                      
                        <label>About Me</label>
                        <Input
                          cols="80"
                          value={aboutMe}
                          placeholder=""
                          rows="2"
                          type="textarea"
                        />
                      
                    </Col>
                    <Col className="px-md-1" md="4">
                        <label>Country</label>
                        <div className="userprofileservicetype">
                          <Select native  label="Service Type" placeholder="Select service Type" onChange={ e=> setServiceType(e.target.value)}>
                            <option value={'mortgage'}>Mortgage</option>
                            <option value={'banking'}>Banking</option>
                            <option value={'wealthmanagement'}>Wealth Management</option>
                            <option value={'insurance'}>Insurance</option>
                            <option value={'taxation'}>Taxation</option>
                            <option value={'stocktrading'}>Stock Trading</option>
                            <option value={'investmentfunds'}>Investment Funds</option>
                            <option value={'retirementplanning'}>Retirement Planning</option>
                          </Select>
                        </div>
                    </Col>
                  </Row>

                
          </Col>
          <Col md="3">
            <Row>
            <Col className="px-md-1" md="12">
                <div className="authorImage">
                <img  src={imageURL}
                      alt="..."
                      className="avatar"
                    />
                    <h5 className="title">{displayName}</h5>
                </div>
               </Col> 
            </Row>
          </Col>
        </Row>
        </div>
          );

}

